export const API_URL = `https://larek-api.nomoreparties.co/api/weblarek`;
export const CDN_URL = `https://larek-api.nomoreparties.co/content/weblarek`;
export const settings = {};
export function getCategoryClass(value) {
    switch (value) {
        case 'софт-скил':
            return 'soft';
        case 'другое':
            return 'other';
        case 'дополнительное':
            return 'additional';
        case 'кнопка':
            return 'button';
        case 'хард-скил':
            return 'hard';
        default:
            return value;
    }
}
